<template>
  <div class="verticalBar">
    <div id="myChart" ref="chart" style="width:100%;height:100%"></div>
  </div>
</template>

<script>
import { getPreMarkAndRedlineTotal } from '@/api/acquisitionTable'
export default {
  name: 'VerticalBar',
  data () {
    return {
      myChart: {},
      colors: ['#d14a61', '#5793f3'],
      data: ''
    }
  },
  watch: {
    colors: function (val) {
      this.myChart.setOption(this.option, true)
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.data.redlinecount) {
        if (item.NAME) {
          Arr.push(item.NAME)
        }
      }
      return Arr
    },
    premarkercount: function () {
      let Arr = []
      for (let item of this.data.premarkercount) {
        Arr.push(item.makerSum)
      }
      return Arr
    },
    redlineSum: function () {
      let Arr = []
      for (let item of this.data.redlinecount) {
        Arr.push(item.redlineSum)
      }
      return Arr
    }
  },
  mounted () {
    this.getAreas()
  },
  methods: {
    /* 获取数据 */
    getAreas () {
      getPreMarkAndRedlineTotal({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          this.data = res.data.data
          this.drawLine()
        }
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      this.option = {
        color: this.colors,
        title: {
          text: '预设界桩数量统计',
          subtext: '',
          textStyle: {
            fontSize: 18
          },
          left: 30,
          top: 0
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: ['红线斑块数量', '预设界桩数量'],
          top: 0,
          right: 30
        },
        grid: {
          left: '5%',
          right: '11%',
          bottom: '5%',
          top: '12%',
          containLabel: true
        },
        toolbox: {
          show: true,
          feature: {
            magicType: { show: true, type: ['line', 'bar'] },
            restore: { show: true },
            saveAsImage: { show: true }
          },
          right: 20,
          top: 40
        },
        calculable: true,
        xAxis: [
          {
            type: 'category',
            data: this.xAxisData
          }
        ],
        yAxis: [
          {
            type: 'value',
            boundaryGap: [0, 0.01]
          }
        ],
        series: [
          {
            name: '红线斑块数量',
            type: 'bar',
            data: this.redlineSum
          },
          {
            name: '预设界桩数量',
            type: 'bar',
            data: this.premarkercount
          }
        ]
      }
      // 绘制图表
      this.myChart.setOption(this.option)
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    }
  }
}
</script>

<style lang="scss" scoped>
  .verticalBar{
    width: 100%;
    height: 100%;
    .color_box{
      position: absolute;
      right:127px;
      top:42px;
      .iconBtn {
        padding: 2px 7px 2px 7px;
        cursor: pointer;
        color: #000;
        &:hover{
          color:rgb(62, 152, 197)
        }
      }
    }
  }
</style>
