<template>
  <div id="tabletRegister">
    <h2>生态保护红线斑块登记表</h2>
    <div>
      <table border='0' cellpadding='0' cellspacing='0' id="tab">
        <tr>
          <td colspan='6' style="width: 25%">红线编码</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldNumber}}</td>
          <td colspan='6' style="width: 25%">名称</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldName}}</td>
        </tr>
        <tr>
          <td colspan='6'>地理位置</td>
          <td colspan='18' style="font-weight: normal">{{data.srldPosition}}</td>
        </tr>
        <tr>
          <td colspan='6' style="width: 25%">区域性红线</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.areaRedline}}</td>
          <td colspan='6' style="width: 25%">生态植被</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldPlantType}}</td>
        </tr>
        <tr>
          <td colspan='6' style="width: 25%">区域面积(平方千米)</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldArea}}</td>
          <td colspan='6' style="width: 25%">人口</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldPopulation}}</td>
        </tr>
        <tr>
          <td colspan='6' style="width: 25%">类型</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{getType(data.srldType)}}</td>
          <td colspan='6' style="width: 25%">生态系统服务功能与保护目标</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{getTarget(data.target)}}</td>
        </tr>
        <tr>
          <td colspan='6'>生态系统与植被类型</td>
          <td colspan='18' style="font-weight: normal;text-align: left">{{data.srldPlantType}}</td>
        </tr>
        <tr>
          <td colspan='6'>主要人为活动</td>
          <td colspan='18' style="font-weight: normal;text-align: left">{{data.srldActive}}</td>
        </tr>
        <tr>
          <td colspan='6'>生态环境问题</td>
          <td colspan='18' style="font-weight: normal;text-align: left">{{data.srldProblem}}</td>
        </tr>
        <tr>
          <td colspan='6'>管控措施</td>
          <td colspan='18' style="font-weight: normal;text-align: left">{{data.srldControl}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import { renderTime } from '@/utils/common'

export default {
  name: 'TabletRegister',
  data () {
    return {
      /* 编辑数据 */
      data: {},
      type: ''
    }
  },
  computed: {},
  props: {
    tabletData: {},
    /* 行政区划 */
    codeData: {}
  },
  watch: {
    tabletData () {
      this.getTableData()
    }
  },
  mounted () {},
  methods: {
    /* 日期格式转换 */
    renderTime (date) {
      return renderTime(date)
    },
    /* 类型转换 */
    getType (data) {
      this.type = data
      if (data === '01') {
        return '生态功能重要区'
      } else if (data === '02') {
        return '生态环境敏感区'
      } else {
        return ''
      }
    },
    /* target转换 */
    getTarget (data) {
      if (this.type === '01') {
        if (data === '01') {
          return '水源涵养'
        } else if (data === '02') {
          return '生物多样性维护'
        } else if (data === '03') {
          return '水土保持'
        } else if (data === '04') {
          return '防风固沙'
        } else if (data === '05') {
          return '其他生态功能'
        }
      } else if (this.type === '02') {
        if (data === '01') {
          return '水土流失'
        } else if (data === '02') {
          return '土地沙化'
        } else if (data === '03') {
          return '石漠化'
        } else if (data === '04') {
          return '其他敏感性'
        }
      }
    },
    /* 获取原始界桩列表 */
    getTableData () {
      this.data = this.tabletData
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  h2{
    padding:5px 0 0;
    font-weight: normal
  }
  #tabletRegister {
    height: 100%;
    overflow: hidden;
    &>div {
      height: calc(100% - 41px);
      overflow: auto;
    }
  }
  #tab{
    border-collapse: collapse;
    table-layout: fixed;
    text-align: center;
    width: calc(100% - 16px);
    margin: 8px;
    border:solid #d1d1d1; border-width:1px 0 0 1px;
    td{
      padding: 10px 5px;
      font-size: 12px;
      font-weight: bolder;
      border:solid #d1d1d1;
      border-width:0 1px 1px 0;
    }
  }
</style>
