<template>
  <div id="mobileUserEdit">
    <Modal
      v-model="pointRebuild"
      title="编辑预设拐点信息">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="110" style="width: 80%;margin: auto">
        <FormItem prop="id" label="ID：" style="display: none">
          {{EditData.id}}
        </FormItem>
        <FormItem prop="pointNum" label="预设拐点编码：">
          <Input v-model.trim="EditData.pointNum" placeholder="请输入预设拐点编码" size="default"/>
        </FormItem>
        <FormItem prop="longitude" label="经度：">
          <Input v-model.trim="EditData.longitude" size="default" disabled/>
        </FormItem>
        <FormItem prop="latitude" label="纬度：">
          <Input v-model.trim="EditData.latitude" size="default" disabled/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { updatePoint } from '@/api/acquisitionTable'
export default {
  name: 'point_edit',
  data () {
    return {
      EditRule: {
        pointNum: [
          { required: true, message: '预设拐点编码不能为空', trigger: 'blur' }
        ]
      },
      /* 编辑框 */
      pointRebuild: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 编辑行原数据 */
    pointInfoData: {}
  },
  watch: {
    pointInfoData (val) {
      // this.dataHandle()
    }
  },
  mounted () {
  },
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle (val) {
      this.EditData.id = val.lpId
      this.$set(this.EditData, 'pointNum', val.lpCode)
      this.EditData.longitude = val.lpLon
      this.EditData.latitude = val.lpLat
    },
    /* 编辑移动端用户 */
    editSubmit () {
      this.$refs.edit.validate(valid => {
        if (valid) {
          updatePoint({
            data: {
              id: this.EditData.id,
              pointNum: this.EditData.pointNum,
              longitude: this.EditData.longitude,
              latitude: this.EditData.latitude
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '编辑预设拐点信息成功' })
              this.$emit('getTableData')
              this.pointRebuild = false
              this.EditData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {
        id: '',
        pointNum: '',
        longitude: '',
        latitude: ''
      }
      this.$refs.edit.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
