<template>
  <div class="mapTable" >
    <div class="btnBox" v-show="!spotTable" @click="spotTable=true">
      <i-button icon="ios-chatboxes"></i-button>
    </div>
    <div class="modelBox" v-if="spotTable">
      <p>
        <strong :class="{ active : tabState === 1 }" @click="switchTab(1)">
          红线斑块
        </strong>
        <strong :class="{ active : tabState === 2 }" @click="switchTab(2)">
          预设界桩
        </strong>
        <strong :class="{ active : tabState === 3 }" @click="switchTab(3)">
          预设标识牌
        </strong>
        <strong :class="{ active : tabState === 0 }"  @click="switchTab(0)">
          预设拐点
        </strong>
        <strong :class="{ active : tabState === 4 }" @click="switchTab(4)">
          数据统计
        </strong>
        <Button type="primary" size="small" v-if="tabState === 0" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportPointExcel" class="exportBtn">导出</Button>
        <Button type="primary" size="small" v-if="tabState === 1" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportRedLineExcel" class="exportBtn">导出</Button>
        <Button type="primary" size="small" v-if="tabState === 2" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportPreExcel" class="exportBtn">导出</Button>
        <Button type="primary" size="small" v-if="tabState === 3" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportBoardPreExcel" class="exportBtn">导出</Button>
      </p>
      <!--拐点坐标-->
      <div class="table_box" v-if="tabState === 0">
        <Table :loading="loading1" border stripe :columns="columns1" :data="data1" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total1" :page-size="pageSize1"  size="small" class="thePage" @on-change="pageChange1" :current="pageNum1" show-total></Page>
        <!-- 拐点信息修改-->
        <pointInfoEdit ref="pointInfoEdit" :redLineInfoData="pointInfoData" @getTableData="getTableData"></pointInfoEdit>
      </div>
      <!--红线台账-->
      <div class="table_box" v-if="tabState === 1">
        <Table :loading="loading2" border stripe :data="data2" :columns="columns2" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total2" :page-size="pageSize2"  size="small" class="thePage" @on-change="pageChange2" :current="pageNum2" show-total></Page>
        <!--红线台账登记表-->
        <div class="extract_box" :class="{regActive: registerTab}">
          <Button type="primary" @click="registerTab = false" size="small" style="position: absolute;right:8px;top:8px;">返回</Button>
          <redLineRegister :tabletData="tabletData" style="background: #fff;"></redLineRegister>
        </div>
        <!-- 红线台账信息修改-->
        <redLineInfoEdit ref="redLineInfoEdit" :redLineInfoData="redLineInfoData" @getTableData="getRedlineData"></redLineInfoEdit>
      </div>
      <!--界碑界桩-->
      <div class="table_box" v-if="tabState === 2">
        <Table  :loading="loading3" border stripe :columns="columns3" :data="data3" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total3" :page-size="pageSize3"  size="small" class="thePage" @on-change="pageChange3" :current="pageNum3" show-total></Page>
        <!-- 界碑界桩信息修改-->
        <PreMarker ref="PreMarkerEdit" :PreMarkerData="PreMarkerData" :codeData="codeData" :redlineList="redlineList" @getTableData="getTableData3"></PreMarker>
      </div>
      <!--标识标牌-->
      <div class="table_box" v-if="tabState === 3">
        <Table :loading="loading4" border stripe :columns="columns4" :data="data4" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total4" :page-size="pageSize4"  size="small" class="thePage" @on-change="pageChange4" :current="pageNum4" show-total></Page>
        <!-- 标识标牌信息修改-->
        <PretLmBoard ref="PretLmBoardEdit" :PretLmBoardData="PretLmBoardData" :redlineList="redlineList" @getTableData="getTableData4"></PretLmBoard>
      </div>
      <!--统计-->
      <div class="table_box" v-if="tabState === 4">
        <Form :model="statisticForm" :label-width="90" class="statisticForm">
          <FormItem prop="city" label="统计类型：">
            <i-select v-model.trim="statisticForm.statisticType" style="width: 100%" placeholder="请选择统计类型" @on-change="changeStatisticType">
              <i-option v-for="item in statisticList" :value="item.value">{{ item.label }}</i-option>
            </i-select>
          </FormItem>
        </Form>
        <div class="statisticBox">
          <!--拐点-->
          <BarChart1 ref="BarChart1" v-if="statisticForm.statisticType === '0'"></BarChart1>
          <!--红线-->
          <BarChart2 ref="BarChart2" v-else-if="statisticForm.statisticType === '1'"></BarChart2>
          <!--界碑界桩-->
          <BarChart3 ref="BarChart3" v-else-if="statisticForm.statisticType === '2'"></BarChart3>
          <!--标识牌-->
          <BarChart4 ref="BarChart4" v-else-if="statisticForm.statisticType === '3'"></BarChart4>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  /**
   * 拐点相关接口
   */
  getPointList,
  exportPointExcel,
  /**
   * 界碑界桩相关接口
   */
  getPreMarkerList,
  exportPreExcel,
  /**
   * 标识标牌相关接口
   */
  gePretLmBoardList,
  exportBoardPreExcel,
  /**
   * 红线相关接口
   */
  redlineData,
  // 行政区划
  getMenu,
  // 全部红线斑块
  allRedlLine,
  // 红线斑块导出
  exportRedLineExcel
} from '@/api/acquisitionTable'
import { getToken } from '@/utils/auth'
import { cacuLonLat } from '@/utils/common'
import BarChart1 from './BarChart1.vue'
import BarChart2 from './BarChart2.vue'
import BarChart3 from './BarChart3.vue'
import BarChart4 from './BarChart4.vue'
// 红线登记表
import redLineRegister from './redLine_register'
// 红线台账信息修改
import redLineInfoEdit from './redLine_edit'
// 标识标牌信息修改
import PretLmBoard from './PretLmBoard_edit'
// 界碑界桩信息修改
import PreMarker from './PreMarker_edit'
// 拐点信息修改
import pointInfoEdit from './point_edit'
export default {
  name: 'MapTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32,
      /* 列表框打开 */
      spotTable: true,
      /* 列表伸缩框打开 */
      telescopic: true,
      /**
       * 拐点相关参数
       */
      /* 原始界桩表头 */
      columns1: [
        {
          title: '拐点编码',
          key: 'lpCode',
          width: 180,
          fixed: 'left',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd(params.row.lpLon, params.row.lpLat, params.row.lpId, 'inflectionPoint')
                  }
                }
              }, params.row.lpCode)
            ])
          }
        },
        {
          title: '经度',
          key: 'lpLon',
          minWidth: 120,
          align: 'center',
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.lpLon))
          }
        },
        {
          title: '纬度',
          key: 'lpLat',
          minWidth: 120,
          align: 'center',
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.lpLat))
          }
        },
        {
          title: '操作',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('div', [
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.pointInfoData = params.row
                    this.$refs.pointInfoEdit.pointRebuild = true
                    this.$refs.pointInfoEdit.dataHandle(params.row)
                    console.log(this.pointInfoData)
                  }
                }
              }, '编辑')
            ])
          }
        }
      ],
      /* 表格数据 */
      data1: [],
      /* 页码 */
      pageNum1: 1,
      /* 每页条数 */
      pageSize1: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /* 总条数 */
      total1: 0,
      /* 加载 */
      loading1: false,
      /**
       * 红线图斑相关参数
       */
      /* 加载 */
      loading2: false,
      /* 页码 */
      pageNum2: 1,
      /* 每页条数 */
      pageSize2: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /* 总数 */
      total2: 0,
      /* 红线图斑数据 */
      data2: [],
      /* 红线台账表格列配置项 */
      columns2: [
        {
          title: '红线编码',
          key: 'srldNumber',
          align: 'center',
          width: 150,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd2(params.row.srldId)
                  }
                }
              }, params.row.srldNumber)
            ])
          }
        },
        {
          title: '斑块名称',
          key: 'srldName',
          align: 'center',
          width: 230
        },
        {
          title: '生态植被',
          key: 'srldPlantType',
          align: 'center',
          width: 150
        },
        {
          title: '功能属性',
          key: 'srldTarget',
          align: 'center',
          minWidth: 200,
          render: (h, params) => {
            let str = ''
            if (params.row.srldType === '01') {
              str += '生态功能重要区'
              if (params.row.target === '01') {
                str += '水源涵养'
              } else if (params.row.target === '02') {
                str += '生物多样性维护'
              } else if (params.row.target === '03') {
                str += '水土保持'
              } else if (params.row.target === '04') {
                str += '防风固沙'
              } else if (params.row.target === '05') {
                str += '其他生态功能'
              }
            } else if (params.row.srldType === '02') {
              str += '生态环境敏感区'
              if (params.row.target === '01') {
                str += '水土流失'
              } else if (params.row.target === '02') {
                str += '土地沙化'
              } else if (params.row.target === '03') {
                str += '石漠化'
              } else if (params.row.target === '04') {
                str += '其他敏感性'
              }
            }
            return h('div', str)
          }
        },
        {
          title: '面积(平方千米)',
          key: 'srldArea',
          align: 'center',
          minWidth: 120
        },
        {
          title: '登记表',
          key: 'isBs',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.tabletData = params.row
                    this.registerTab = true
                  }
                }
              }, '登记表')
            ])
          }
        },
        {
          title: '操作',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            return h('div', [
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.redLineInfoData = params.row
                    this.$refs.redLineInfoEdit.redLineRebuild = true
                  }
                }
              }, '编辑')
            ])
          }
        }
      ],
      /**
       * 界碑界桩相关参数
       */
      /* 原始界桩表头 */
      columns3: [
        {
          title: '预设界桩编码',
          key: 'jzNumber',
          align: 'center',
          width: 180,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd3(params.row.longitude, params.row.latitude, params.row.objectId, 'inflectionJiebei')
                  }
                }
              }, params.row.jzNumber)
            ])
          }
        },
        {
          title: '预设界桩刻号',
          key: 'jzKh',
          align: 'center',
          minWidth: 180
        },
        {
          title: '所属红线斑块',
          key: 'redlineName',
          align: 'center',
          minWidth: 170
        },
        {
          title: '行政区划',
          key: 'placeName',
          align: 'center',
          minWidth: 150
        },
        {
          title: '操作',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('div', [
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.PreMarkerData = params.row
                    this.$refs.PreMarkerEdit.PreMarkerRebuild = true
                  }
                }
              }, '编辑')
            ])
          }
        }
      ],
      /* 表格数据 */
      data3: [],
      /* 页码 */
      pageNum3: 1,
      /* 每页条数 */
      pageSize3: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /* 总条数 */
      total3: 0,
      /* 加载 */
      loading3: false,
      /* 加载 */
      /**
       * 标识标牌相关参数
       */
      /* 标识牌表头 */
      columns4: [
        {
          title: '预设标识牌编码',
          key: 'number',
          align: 'center',
          width: 150,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd4(params.row.longitude, params.row.latitude, params.row.objectId, 'inflectionBiaopai')
                  }
                }
              }, params.row.number)
            ])
          }
        },
        {
          title: '所属红线斑块',
          key: 'redlineName',
          align: 'center',
          minWidth: 200
        },
        {
          title: '经度',
          key: 'longitude',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.longitude))
          }
        },
        {
          title: '纬度',
          key: 'latitude',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.latitude))
          }
        },
        {
          title: '操作',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('div', [
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.PretLmBoardData = ''
                    this.PretLmBoardData = params.row
                    this.$refs.PretLmBoardEdit.PretLmBoardRebuild = true
                  }
                }
              }, '编辑')
            ])
          }
        }
      ],
      /* 表格数据 */
      data4: [],
      /* 页码 */
      pageNum4: 1,
      /* 每页条数 */
      pageSize4: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /* 总条数 */
      total4: 0,
      /* 加载 */
      loading4: false,
      /* tab选项卡状态 */
      tabState: 1,
      // 统计
      statisticForm: {
        statisticType: '1'
      },
      // 统计类型列表
      statisticList: [
        {
          label: '预设拐点',
          value: '0'
        },
        {
          label: '红线斑块',
          value: '1'
        },
        {
          label: '预设界桩',
          value: '2'
        },
        {
          label: '预设标识牌',
          value: '3'
        }
      ],
      /* 登记表框状态 */
      registerTab: false,
      /* 登记表查询数据 */
      tabletData: {},
      /* 红线台账编辑框默认原数据 */
      redLineInfoData: {},
      /* 标识标牌编辑框默认原数据 */
      PretLmBoardData: {},
      /* 界碑界桩编辑框默认原数据 */
      PreMarkerData: {},
      /* 拐点编辑框默认原数据 */
      pointInfoData: {},
      /* 红线斑块列表 */
      redlineList: [],
      /* 行政区划数据 */
      codeData: []
    }
  },
  props: {
    /* 行政区划 */
    obj: {
      default: {
        code: '',
        param: ''
      }
    },
    param: {},
    srId: null,
    dataChange: {}
  },
  components: {
    BarChart1,
    BarChart2,
    BarChart3,
    BarChart4,
    // 红线登记表
    redLineRegister,
    // 红线台账信息修改
    redLineInfoEdit,
    // 标识标牌信息修改
    PretLmBoard,
    // 界碑界桩信息修改
    PreMarker,
    // 拐点信息修改
    pointInfoEdit
  },
  watch: {
    // 根据查询条件显示不同复合条件的列表
    obj () {
      if (this.tabState === 0) {
        this.data1 = []
        this.getTableData()
      } else if (this.tabState === 1) {
        this.data2 = []
        this.getRedlineData()
      } else if (this.tabState === 2) {
        this.data3 = []
        this.getTableData3()
      } else if (this.tabState === 3) {
        this.data4 = []
        this.getTableData4()
      }
    },
    /**
     * 拐点相关参数
     */
    srId (val) {
      if (this.tabState === 0) {
        this.pageNum1 = 1
        this.getTableData()
      } else if (this.tabState === 1) {
        this.pageNum2 = 1
        this.getRedlineData()
      } else if (this.tabState === 2) {
        this.pageNum3 = 1
        this.getTableData3()
      } else if (this.tabState === 3) {
        this.pageNum4 = 1
        this.getTableData4()
      }
    },
    pageNum1 () {
      this.getTableData()
    },
    spotTable (val) {
      this.$emit('mapSizeChange', val)
    },
    /**
     * 界碑界桩相关参数
     */
    pageNum3 () {
      this.getTableData3()
    },
    /**
     * 标识标牌相关参数
     */
    pageNum4 () {
      this.getTableData4()
    },
    /**
     * 红线
     */
    pageNum2 () {
      this.getRedlineData()
    }
  },
  created () {
    this.switchTab(this.tabState)
  },
  mounted () {
    /* 监听浏览器高度 */
    window.addEventListener('resize', () => {
      // 表格自适应高度
      this.tableHeight = Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32
      // 根据tab选项卡状态不同，不同表格页容量自适应数量
      this.pageSize1 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize2 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize3 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize4 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.switchTab(this.tabState)
    }, false)
    // 行政区划
    this.getCodeMenu()
  },
  methods: {
    /* 获取拐点坐标列表 */
    getTableData () {
      this.loading1 = true
      getPointList({
        accessToken: getToken(),
        validate: true,
        data: {
          pageNum: this.pageNum1,
          pageSize: this.pageSize1,
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        this.data1 = res.data.data.rows
        this.total1 = res.data.data.total
        // 点击切换拐点坐标，将对应图层显示在地图
        this.$emit('getPointData', res.data.data.rows, 'inflectionPoint')
        this.loading1 = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /* 分页跳转 */
    pageChange1 (e) {
      this.pageNum1 = e
    },
    /* 导出拐点列表文件 */
    exportPointExcel () {
      exportPointExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = '/epr' + url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /**
     * 界碑界桩相关方法
     */
    /* 获取预设界桩列表 */
    getTableData3 () {
      this.loading3 = true
      getPreMarkerList({
        accessToken: getToken(),
        validate: true,
        data: {
          pageNum: this.pageNum3,
          pageSize: this.pageSize3,
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        this.data3 = res.data.data.rows
        this.total3 = res.data.data.total
        // 点击切换界桩，将对应图层显示在地图
        this.$emit('getPointData', res.data.data.rows, 'inflectionJiebei')
        this.loading3 = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd3 (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /* 分页跳转 */
    pageChange3 (e) {
      this.pageNum3 = e
    },
    /* 导出预设界桩界桩列表文件 */
    exportPreExcel () {
      exportPreExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = '/epr' + url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /**
     * 标识标牌相关方法
     */
    /* 获取标识牌列表 */
    getTableData4 () {
      this.loading4 = true
      gePretLmBoardList({
        data: {
          pageNumber: this.pageNum4,
          pageSize: this.pageSize4,
          boardNum: this.obj.param,
          code: this.obj.code
        }
      }).then((res) => {
        this.data4 = res.data.data.row
        this.total4 = res.data.data.total
        // 点击切换标识标牌，将对应图层显示在地图
        this.$emit('getPointData', res.data.data.row, 'inflectionBiaopai')
        this.loading4 = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd4 (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /* 分页跳转 */
    pageChange4 (e) {
      this.pageNum4 = e
    },
    /* 导出预设标识牌列表文件 */
    exportBoardPreExcel () {
      exportBoardPreExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = '/epr' + url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取红线图斑数据 */
    getRedlineData () {
      this.loading2 = true
      redlineData({
        data: {
          pageNum: this.pageNum2,
          pageSize: this.pageSize2,
          code: this.obj.code,
          redLineNum: this.obj.param
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.total2 = res.data.data.total
          this.data2 = res.data.data.rows
          // 点击切换标识标牌，将对应图层显示在地图
          this.$emit('getPointData', res.data.data.rows, '')
          this.loading2 = false
        }
      })
    },
    /* 点击定位到红线斑块 */
    ondbclickTd2 (id) {
      this.$emit('clickRedLineTable', id)
    },
    /* 分页跳转 */
    pageChange2 (e) {
      this.pageNum2 = e
    },
    /* 切换拐点、红线、界碑界桩、标识标牌 */
    switchTab (state) {
      this.$emit('update:dataChange', state)
      this.tabState = state
      if (state === 0) {
        this.getTableData()
      } else if (state === 1) {
        this.getRedlineData()
      } else if (state === 2) {
        this.getTableData3()
      } else if (state === 3) {
        this.getTableData4()
        /* axios: 获取红线斑块列表 */
        this.getRedLineList()
      }
    },
    /* 统计类型切换事件 */
    changeStatisticType (val) {
     console.log(val)
    },
    /* axios: 获取红线斑块列表 */
    getRedLineList () {
      allRedlLine({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.redlineList = res.data.data
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 获取省市县菜单 */
    getCodeMenu () {
      getMenu({
        data: {
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.codeData = res.data.data
          this.codeData = this.valComCode(this.codeData)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 省市县菜单value = comCode */
    valComCode (arr) {
      for (let i in arr) {
        arr[i].value = arr[i].comCode
        this.valComCode(arr[i].children)
      }
      return arr
    },
    /* 导出红线列表文件 */
    exportRedLineExcel () {
      exportRedLineExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = '/epr' + url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

