<template>
  <div style="height: 100%">
    <div id="viewDiv">
    </div>
    <!-- 地图的工具框 -->
    <ButtonGroup id="toolbar" vertical style="display: flex;flex-direction: column">
      <Tooltip content="距离测量" placement="left">
        <Button :type="type=='distance'?'primary':'default'" @click="clickToolbar('distance')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/length.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
      <Tooltip content="测量面积" placement="left">
        <Button :type="type=='area'?'primary':'default'" @click="clickToolbar('area')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/area.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
    </ButtonGroup>
    <!-- 图例 -->
    <div id="legendbar">
      <Legend :data="legendData"></Legend>
    </div>
  </div>
</template>

<script>
import config from '../../setting/mapConfig'
import arcgisPackage from '../../utils/arcgisTools'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie, markerLayer, bspLayer } from '@/utils/layers'
import { getRedlinePoint } from '../../api/acquisitionTable'
import guaiPoint3 from '../../assets/boundaryMarker_images/guai_point3.png'
import Basemapthumbnail from '../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../assets/map_images/Basemapthumbnail1.png'
import Legend from '@/components/Legend'
/**
 * 界碑界桩相关方法
 */
import presetBm3 from '../../assets/boundaryMarker_images/preset_bm3.png'
/**
 * 标识标牌相关方法
 */
import presetSign3 from '../../assets/boundaryMarker_images/preset_sign3.png'
// 定义红线图层
let layerRedLineView = null
let layerRedLine = null
// 用于红线斑块定位watch函数的flag
let redLineWatchFlag = false
// 用于判断是否触发了红线定位
let toRedLIineflag = null
// 数据图层
let datalayer = null
// 当前使用图层
let currentLayer = null
// 到点的id
let toPointId = null
export default {
  name: 'InflectionPointMap',
  data () {
    return {
      redlinePoint: [], // 存储红线点的拐点集合
      activeWidget: null, // 当前激活的测量工具
      type: '', // 正在使用的测量工具标识
      legendData1: [
        {
          type: 0,
          name: '红线斑块',
          backgroundColor: 'rgba(255, 255, 255, 0)',
          border: '2px solid red',
          id: '0',
          switch: true,
          switchDis: true
        },
        {
          type: 1,
          name: '预设拐点',
          background: 'url(' + guaiPoint3 + ')',
          id: 'inflectionPoint',
          switch: true,
          switchDis: true
        }
      ],
      legendData2: [
        {
          type: 0,
          name: '红线斑块',
          backgroundColor: 'rgba(255, 255, 255, 0)',
          border: '2px solid red',
          id: '0',
          switch: true,
          switchDis: true
        }
      ],
      legendData3: [
        {
          type: 0,
          name: '红线斑块',
          backgroundColor: 'rgba(255, 255, 255, 0)',
          border: '2px solid red',
          id: '0',
          switch: true,
          switchDis: true
        },
        {
          type: 1,
          name: '预设界桩',
          background: 'url(' + presetBm3 + ')',
          id: 'inflectionJiebei',
          switch: true,
          switchDis: true
        }
      ],
      legendData4: [
        {
          type: 0,
          name: '红线斑块',
          backgroundColor: 'rgba(255, 255, 255, 0)',
          border: '2px solid red',
          id: '0',
          switch: true,
          switchDis: true
        },
        {
          type: 1,
          name: '预设标识牌',
          background: 'url(' + presetSign3 + ')',
          id: 'inflectionBiaopai',
          switch: true,
          switchDis: true
        }
      ],
      legendData: [],
      popupTemplate: {}, // 点击地图中点的弹框对象
      popupTemplate3: {}, // 点击地图中点的弹框对象
      popupTemplate4: {}, // 点击地图中点的弹框对象
      loadComplete: false // 地图加载完成状态
    }
  },
  props: {
    pointData: {},
    layerId: {},
    dataChange: {}
  },
  components: {
    Legend
  },
  watch: {
    // 监听不同图层layerId，执行showLayer（）
    layerId (val) {
      if (!this.map) {
        return
      }
      this.showLayer(val)
    },
    legendData: {
      handler (val) {
        for (let i in val) {
          this.map.findLayerById(val[i].id).visible = val[i].switch
        }
      },
      immediate: false,
      deep: true
    },
    dataChange: async function (val) {
      if (this.map) {
        this.map.remove(datalayer)
      }
      switch (val) {
        case 2:
          datalayer = await markerLayer()
          this.map.add(datalayer)
          break
        default:
          break
      }
    }
  },
  async mounted () {
    await this.createMap()
    // 初始化根据不同图层layerId，执行showLayer（）
    this.showLayer(this.layerId)
  },
  methods: {
    // 根据不同图层layerId，地图上添加不同的图层/显示不同图例
    showLayer (id) {
      if (id === 'inflectionPoint') {
        this.legendData = this.legendData1
        this.addPointToMap(this.pointData, config.inflectionPointcolor, id)
        this.addJiebeiToMap('', config.inflectionPointcolor, 'inflectionJiebei')
        this.addBiaopaiToMap('', config.inflectionPointcolor, 'inflectionBiaopai')
      } else if (id === '') {
        this.legendData = this.legendData2
        this.addPointToMap('', config.inflectionPointcolor, 'inflectionPoint')
        this.addJiebeiToMap('', config.inflectionPointcolor, 'inflectionJiebei')
        this.addBiaopaiToMap('', config.inflectionPointcolor, 'inflectionBiaopai')
      } else if (id === 'inflectionJiebei') {
        this.legendData = this.legendData3
        this.addJiebeiToMap(this.pointData, config.inflectionPointcolor, id)
        this.addPointToMap('', config.inflectionPointcolor, 'inflectionPoint')
        this.addBiaopaiToMap('', config.inflectionPointcolor, 'inflectionBiaopai')
      } else if (id === 'inflectionBiaopai') {
        this.legendData = this.legendData4
        this.addPointToMap('', config.inflectionPointcolor, 'inflectionPoint')
        this.addJiebeiToMap('', config.inflectionPointcolor, 'inflectionJiebei')
        this.addBiaopaiToMap(this.pointData, config.inflectionPointcolor, id)
      }
    },
    async createMap (resolve) {
      // 取出arcgis中需要的类
      let Basemap = await arcgisPackage.Basemap
      let Map = await arcgisPackage.Map
      let MapView = await arcgisPackage.MapView
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })
      // 省界
      let layerProvince = await shengjie().then(res => { return res })
      // 红线斑块图层
      layerRedLine = await redLine().then(res => { return res })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, layerProvince, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, layerProvince, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      this.map = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 6
      })

      // 添加图层
      this.map.layers.add(layerRedLine)

      // 添加全屏
      let fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.when(wes => {
        this.loadComplete = true
      })
      this.popupTemplate = {
        title: '{name}',
        content: [
          {
            type: 'fields',
            fieldInfos: [
              {
                fieldName: 'lpCode',
                label: '拐点编码'
              },
              {
                fieldName: 'lpLon',
                label: '经度'
              },
              {
                fieldName: 'lpLat',
                label: '纬度'
              }
            ]
          }
        ]
      }
      // 界碑界桩
      this.popupTemplate3 = {
        title: '{name}',
        content: [
          {
            type: 'fields',
            fieldInfos: [
              {
                fieldName: 'jzNumber',
                label: '界桩编码'
              },
              {
                fieldName: 'placeName',
                label: '行政区划'
              },
              {
                fieldName: 'longitude',
                label: '经度'
              },
              {
                fieldName: 'latitude',
                label: '纬度'
              }
            ]
          }
        ]
      }
      // 标识标牌
      this.popupTemplate4 = {
        title: '{name}',
        content: [
          {
            type: 'fields',
            fieldInfos: [
              {
                fieldName: 'number',
                label: '标识牌编码'
              },
              {
                fieldName: 'placeName',
                label: '行政区划'
              },
              {
                fieldName: 'longitude',
                label: '经度'
              },
              {
                fieldName: 'latitude',
                label: '纬度'
              }
            ]
          }
        ]
      }
      this.view.ui.add(fullscreen, 'top-left')
      // 增加底图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap
      })

      this.view.ui.add(basemapToggle, 'top-right')
      // 添加工具框
      this.view.ui.add(document.getElementById('toolbar'), 'top-right')
      // 添加图例
      this.view.ui.add(document.getElementById('legendbar'), 'bottom-left')

      // let highlight
      // 创建layer查询
      layerRedLineView = await this.view.whenLayerView(layerRedLine)
      layerRedLineView.watch('updating', value => {
        if (!value && toRedLIineflag !== null) {
          let tempID = toRedLIineflag
          toRedLIineflag = null
          let query = layerRedLine.createQuery()
          query.where = 'OBJECTID = ' + tempID + ''
          layerRedLine.queryFeatures(query).then(res => {
            console.log(res)
            this.view.popup.open({
              features: res.features,
              location: res.features[0].geometry.centroid
            })
          })
        }
      })
    },
    /**
     * 添加拐点
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    async addPointToMap (pointArray, symbolColor, layerId) {
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      let Graphic = await arcgisPackage.Graphic
      let layer = this.map.findLayerById(layerId)
      let point = {}
      let pointGraphic
      if (layer) {
        this.map.remove(layer)
      }
      let graphicsLayer = new GraphicsLayer({
        id: layerId,
        title: '拐点',
        elevationInfo: 'on-the-ground'
      })
      let markerSymbol = {
        type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
        url: guaiPoint3,
        height: 16,
        width: 16,
        outline: { // autocasts as new SimpleLineSymbol()
          color: [255, 255, 255],
          width: 1
        }
      }
      if (pointArray && pointArray.length > 0) {
        for (let i = 0; i < pointArray.length; i++) {
          point = {
            type: 'point', // autocasts as new Point()
            longitude: pointArray[i].lpLon,
            latitude: pointArray[i].lpLat
          }
          let lineAtt = {
            lpId: pointArray[i].lpId,
            lpCode: pointArray[i].lpCode,
            lpLat: pointArray[i].lpLat,
            lpLon: pointArray[i].lpLon,
            isActive: false // 用于判断当前拐点是否是激活状态
          }
          pointGraphic = new Graphic({
            geometry: point,
            symbol: markerSymbol,
            attributes: lineAtt,
            popupTemplate: this.popupTemplate
          })
          graphicsLayer.add(pointGraphic)
        }
      }

      this.map.add(graphicsLayer)
    },
    /**
     * 添加界桩
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    async addJiebeiToMap (pointArray, symbolColor, layerId) {

      let layer = this.map.findLayerById(layerId)
      if (layer) {
        this.map.remove(layer)
      }
      if (!pointArray) {
        return
      }
      let graphicsLayer = await markerLayer(layerId)
      this.map.add(graphicsLayer)
      let graphicsLayerView = await this.view.whenLayerView(graphicsLayer)
      graphicsLayerView.watch('updating', res => {
        if (!res && toPointId !== null) {
          let tempid = toPointId
          toPointId = null
          let query = graphicsLayer.createQuery()
          query.where = 'OBJECTID = ' + tempid + 'OR OBJECTID_1 = ' + tempid
          graphicsLayer.queryFeatures(query).then(res => {
            this.view.popup.open({
              features: res.features,
              location: res.features[0].geometry.centroid
            })
          })
        }
      })
    },
    /**
     * 添加标识标牌
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    async addBiaopaiToMap (pointArray, symbolColor, layerId) {
      let layer = this.map.findLayerById(layerId)
      if (layer) {
        this.map.remove(layer)
      }
      if (!pointArray) {
        return
      }
      let graphicsLayer = await bspLayer(layerId)
      this.map.add(graphicsLayer)
      let graphicsLayerView = await this.view.whenLayerView(graphicsLayer)
      graphicsLayerView.watch('updating', res => {
        if (!res && toPointId !== null) {
          let tempid = toPointId
          toPointId = null
          let query = graphicsLayer.createQuery()
          query.where = 'OBJECTID = ' + tempid + ''
          graphicsLayer.queryFeatures(query).then(res => {
            this.view.popup.open({
              features: res.features,
              location: res.features[0].geometry.centroid
            })
          })
        }
      })
    },
    /**
     * 红线双击表格定位
     * @param lon
     * @param lat
     */
    async ondbclickDingwei (lon, lat, id, layerId) {
      currentLayer = await this.map.findLayerById(layerId)
      let query = currentLayer.createQuery()
      query.where = 'OBJECTID = ' + id + 'OR OBJECTID_1 = ' + id
      toPointId = id
      currentLayer.queryFeatures(query).then(res => {
        this.view.goTo({
          target: res.features,
          zoom: 10
        })
      })
    },
    /**
     * @Description:测量工具点击事件
     * @author 张淼
     * @date 2019/4/11
    */
    async clickToolbar (type) {
      if (this.type === type) {
        type = null
      }
      this.type = type
      if (this.activeWidget) {
        this.view.ui.remove(this.activeWidget)
        this.activeWidget.destroy()
        this.activeWidget = null
      }
      switch (type) {
        case 'distance':
          let DistanceMeasurement2D = await arcgisPackage.DistanceMeasurement2D
          this.activeWidget = new DistanceMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        case 'area':
          let AreaMeasurement2D = await arcgisPackage.AreaMeasurement2D
          this.activeWidget = new AreaMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
      }
    },
    /* 点击表格上的红线斑块，高亮显示该红线斑块 */
    async lightRedline (id) {
      console.log(id)
      console.log(layerRedLineView)
      if (layerRedLineView) {
        // 创建查询器
        let query = layerRedLine.createQuery()
        query.where = 'OBJECTID = ' + id + ''
        console.log(layerRedLine)
        layerRedLine.queryFeatures(query).then(async (result) => {
          console.log(result)
          toRedLIineflag = id
          this.view.goTo(result.features[0].geometry)
        })
      }
    }
  }
}

</script>

<style lang="scss" scoped rel="stylesheet/scss">
  #viewDiv{
    height:100%;
    .esri-popup--is-docked {
      right: 70px !important;
    }
  }
</style>
