<template>
  <div id="mobileUserEdit">
    <Modal
      v-model="PreMarkerRebuild"
      title="编辑预设界桩信息"
      @on-cancel="reset">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="100" style="padding:0 50px;">
        <FormItem prop="id" label="ID：" style="display: none">
          {{EditData.id}}
        </FormItem>
        <FormItem prop="jzNumber" label="预设界桩编码：">
          <Input v-model.trim="EditData.jzNumber" size="default"/>
        </FormItem>
        <FormItem prop="jzKh" label="预设界桩刻号：">
          <Input v-model.trim="EditData.jzKh" size="default"/>
        </FormItem>
        <FormItem prop="redlineId" label="所属红线斑块：">
          <Select v-model="EditData.redlineId" size="default" disabled>
            <Option v-for="item in redlineList" :value="item.srldId" :key="item.srldId">{{ item.srldNumber }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="placeId" label="行政区划：">
          <Cascader :data="codeData" v-model="EditData.placeId" size="default" change-on-select></Cascader>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { updateMaker } from '@/api/acquisitionTable'
export default {
  name: 'PreMarker_edit',
  data () {
    return {
      EditRule: {},
      /* 编辑框 */
      PreMarkerRebuild: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 编辑行原数据 */
    PreMarkerData: {},
    /* 红线斑块 */
    redlineList: {},
    /* 行政区划 */
    codeData: {}
  },
  watch: {
    PreMarkerData () {
      this.dataHandle()
    }
  },
  mounted () {
  },
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.EditData.id = this.PreMarkerData.id
      this.EditData.jzNumber = this.PreMarkerData.jzNumber
      this.EditData.jzKh = this.PreMarkerData.jzKh
      this.EditData.redlineId = this.PreMarkerData.redlineId
      let arr = []
      this.getDefaultCode(this.codeData, this.PreMarkerData.code, arr)
    },
    /* 设置编辑框行政区划默认值 */
    getDefaultCode (data, code, arr) {
      for (let i in data) {
        if (data[i].value === code) {
          arr.push(data[i].value)
          this.EditData.placeId = JSON.parse(JSON.stringify(arr))
        } else {
          if (data[0].children.length > 0) {
            arr.push(data[i].value)
            this.getDefaultCode(data[i].children, code, arr)
          }
        }
      }
      arr.pop()
    },
    /* 编辑移动端用户 */
    editSubmit () {
      let code = ''
      if (this.EditData.placeId && this.EditData.placeId.length > 0) {
        code = this.EditData.placeId[this.EditData.placeId.length - 1]
      }
      updateMaker({
        data: {
          id: this.EditData.id,
          jzNum: this.EditData.jzNumber,
          jzkh: this.EditData.jzKh,
          code: code
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '编辑预设界桩信息成功' })
          this.$emit('getTableData')
          this.PreMarkerRebuild = false
          this.EditData = {}
          this.reset()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {
        id: '',
        jzNumber: '',
        jzKh: '',
        redlineId: '',
        placeId: []
      }
      this.$set(this.EditData, 'placeId', '')
      this.$refs.edit.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
