<template>
  <div id="tablet">
    <!-- 拐点查询组件-->
    <InflectionPointQueryBox @searchSrldId="searchSrldId" v-if="layerId === 'inflectionPoint'"></InflectionPointQueryBox>
    <!-- 红线查询组件-->
    <redLineQueryBox @searchSrldId="searchSrldId" v-if="layerId === ''"></redLineQueryBox>
    <!-- 预设标识牌查询组件-->
    <SignBoardQueryBox @searchSrldId="searchSrldId" v-else-if="layerId === 'inflectionBiaopai'"></SignBoardQueryBox>
    <!-- 预设界桩查询组件-->
    <TabletQueryBox @searchSrldId="searchSrldId" v-else-if="layerId === 'inflectionJiebei'"></TabletQueryBox>
    <div class="demo-split">
      <Split v-model="split1" min="200px" max="600px">
        <div slot="left" class="demo-split-pane">
          <Map  ref="map1" :pointData="pointData" :layerId="layerId" @changeredlineid="changeredlineid" :dataChange="dataChange"></Map>
        </div>
        <div slot="right" class="demo-split-pane">
          <InflectionPointTab :obj="obj" :srld="srld" :dataChange.sync="dataChange"
            @ondbclickDingwei="ondbclickDingwei"
            @getPointData="getPointData"
            @mapSizeChange="mapSizeChange"
            @clickRedLineTable="clickRedLineTable"></InflectionPointTab>
        </div>
      </Split>
    </div>
  </div>
</template>

<script>
import Map from '../map/InflectionPoint_map.vue'
import InflectionPointTab from './InflectionPoint_tab.vue'
import InflectionPointQueryBox from './InflectionPoint_query'
import SignBoardQueryBox from './SignBoard_query.vue'
import TabletQueryBox from './Tablet_query'
import redLineQueryBox from './redLine_query'
export default {
  name: 'Tablet',
  data () {
    return {
      /* 查询框返回查询条件 */
      obj: '',
      pointData: [],
      layerId: '',
      lon: '',
      lat: '',
      spotTable: true,
      srld: null, // 当前选择的红线id
      split1: 0.6,
      dataChange: 0 // 用于切换地图上的数据
    }
  },
  watch: {
    // 监听不同图层layerId，清空查询条件
    layerId (val) {
      this.obj = ''
    }
  },
  components: {
    Map,
    InflectionPointTab,
    // 拐点查询组件
    InflectionPointQueryBox,
    // 预设标识牌查询组件
    SignBoardQueryBox,
    // 预设界桩查询组件
    TabletQueryBox,
    // 红线查询组件
    redLineQueryBox
  },
  mounted () {
  },
  methods: {
    /* 双击表格修改选中经纬度 */
    ondbclickDingwei (lon, lat, id, layerId) {
      this.$refs.map1.ondbclickDingwei(lon, lat, id, layerId)
    },
    /* 级联查询返回要搜索的数据 */
    searchSrldId (obj) {
      this.obj = obj
    },
    /* 获取不同图层id/全部点据 */
    getPointData (data, id) {
      this.pointData = data
      this.layerId = id
    },
    /* 控制地图框缩小 */
    mapSizeChange (spotTable) {
      if (spotTable === true) {
        this.spotTable = true
      } else {
        this.spotTable = false
      }
    },
    /**
     * @Description:更改红线id事件的回掉函数
     * @author 张淼
     * @date 2019/4/11
    */
    changeredlineid (id) {
      this.srld = id
    },
    /* 点击红线斑块表格的函数 */
    clickRedLineTable (id) {
      this.$refs.map1.lightRedline(id)
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .map_box{
      position:absolute;
      width:60%;
      top: 55px;
      bottom: 0;
    }
    .cascader_box{
      position: absolute;
      left:70px;
      top:70px;
    }
    .spotTableBox{
      position: absolute;
      right:0;
      top:55px;
      width:40%;
    }
    .demo-split{
      height: calc(100% - 65px);
      padding: 0 15px 15px;
    }
    .demo-split-pane{
      height: 100%;
    }
  }
</style>
