<template>
  <div id="mobileUserEdit">
    <Modal
      v-model="PretLmBoardRebuild"
      title="编辑预设标识牌信息">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="100" style="padding:0 50px;">
        <FormItem prop="id" label="ID：" style="display: none">
          {{EditData.id}}
        </FormItem>
        <FormItem prop="boardNum" label="预设标识牌编码：">
          <Input v-model.trim="EditData.boardNum" size="default"/>
        </FormItem>
        <FormItem prop="redlineId" label="所属红线斑块：">
          <Select v-model="EditData.redlineId" size="default">
            <Option v-for="item in redlineList" :value="item.srldId" :key="item.srldId">{{ item.srldNumber }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="longitude" label="经度：">
          <Input v-model.trim="EditData.longitude" size="default" disabled/>
        </FormItem>
        <FormItem prop="latitude" label="纬度：">
          <Input v-model.trim="EditData.latitude" size="default" disabled/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { updateBoard } from '@/api/acquisitionTable'
export default {
  name: 'PretLmBoard_edit',
  data () {
    return {
      EditRule: {},
      /* 编辑框 */
      PretLmBoardRebuild: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 编辑行原数据 */
    PretLmBoardData: {},
    /* 红线斑块列表 */
    redlineList: {}
  },
  watch: {
    PretLmBoardData () {
      this.dataHandle()
    }
  },
  mounted () {
  },
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.EditData.id = this.PretLmBoardData.id
      this.EditData.boardNum = this.PretLmBoardData.number
      this.EditData.redlineId = this.PretLmBoardData.redlineId
      this.EditData.longitude = this.PretLmBoardData.longitude
      this.EditData.latitude = this.PretLmBoardData.latitude
    },
    /* 编辑移动端用户 */
    editSubmit () {
      this.$refs.edit.validate(valid => {
        if (valid) {
          updateBoard({
            data: {
              id: this.EditData.id,
              boardNum: this.EditData.boardNum,
              redlineId: String(this.EditData.redlineId)
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '编辑预设标识牌信息成功' })
              this.$emit('getTableData')
              this.PretLmBoardRebuild = false
              this.EditData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {
        id: '',
        number: '',
        redlineId: '',
        longitude: '',
        latitude: ''
      }
      this.$refs.edit.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
